import {initializeForms} from './forms'
import './parallax'
import './swiper-slider'
import './notificacion'
import './menus'
import tap from 'tap-logger'

initializeForms();

$(document).ready(function(){
	new WOW().init();
	
  $(window).scroll(function() {
  });
});

// var scene = document.getElementById('scene');
// var parallaxInstance = new Parallax(scene);
window.tap = tap

	var scene = document.getElementById('scene');
	
	var parallax = new Parallax(scene, {
		onReady () {
			let rotationAmount = 20//degs
			let w_width = $(window).width()//hay que recalculararlo cuando haya window.resize
			$(window).on('mousemove', (e) => {
				let rotation = (
					((360 / w_width * (0 + e.pageX)) % 360) //escalamos 360 al ancho de la pantalla, para que el extremo izquierdo sea 0 grados y el derecho 360 grados//el maximo es 359 y si tenemos 360 nos devuelve 0, 361 nos da 1,...
					/ 360 * rotationAmount //escalamos la rotacion al maximo de rotationAmmount
					- (rotationAmount/2)//centramos en 0
				) 
				$('#mundo_container svg').css('transform', `rotate(${rotation}deg)`)

			})
		}
	});


